/**
 * @file src/pages/about-information/index.jsx
 * @description Componente que renderiza la pgina esttica "Acerca de" de la aplicacin Kamikaze.
 * Esta pgina proporciona informacin detallada sobre el juego, sus caractersticas y la comunidad.
 */

import React from 'react';
import { Helmet } from 'react-helmet'; // Importa Helmet para gestionar el <head> del HTML (SEO y ttulo de la pgina).

// --- Importacin de Componentes Reutilizables ---
// Cada uno de estos componentes representa una seccin visual especfica dentro de esta pgina.
import Header from '../../components/ui/Header';
import ContextualBackNavigation from '../../components/ui/ContextualBackNavigation';
import GameDescriptionSection from './components/GameDescriptionSection';
import GameFeaturesGrid from './components/GameFeaturesGrid';
import ChallengeProposalSection from './components/ChallengeProposalSection';
import ResponsibleDrinkingReminder from './components/ResponsibleDrinkingReminder';
import InstallationGuideSection from './components/InstallationGuideSection';
import CommunityGuidelinesSection from './components/CommunityGuidelinesSection';
import Icon from '../../components/AppIcon'; // El componente reutilizable para mostrar conos.
import bgImage from '../../assets/images/graffiti-bg.png';

/**
 * Componente principal de la pgina "Acerca de".
 * Es un componente "presentacional", es decir, no maneja lgica compleja ni estado.
 * Su nica funcin es estructurar y mostrar la informacin usando otros componentes.
 */
const AboutInformation = () => {
    return (
        // Contenedor principal de la pgina con un color de fondo definido en el tema.
        <div className="min-h-screen bg-cover bg-center bg-no-repeat relative overflow-hidden" style={{ backgroundImage: `url(${bgImage})` }}>
            <div className="absolute inset-0 bg-background/80 backdrop-blur-sm"></div>

            {/* --- SEO y Metadatos de la Pgina --- */}
            {/* Este bloque usa React Helmet para inyectar etiquetas en el <head> del HTML. */}
            {/* Es crucial para el posicionamiento en buscadores (SEO) y no es visible en la pgina. */}
            <Helmet>
                <title>Acerca de - Kamikaze Drinking Game</title>
                <meta name="description" content="Descubre todo sobre Kamikaze, el juego de beber ms emocionante. Aprende las reglas, propn nuevos retos y nete a nuestra comunidad." />
                <meta name="keywords" content="kamikaze, juego de beber, retos, fiesta, amigos, PWA, drinking game" />
            </Helmet>

            {/* Renderiza el encabezado principal de la aplicacin (la barra de navegacin superior). */}
            <Header />

            {/* Contenedor del contenido principal de la pgina. */}
            <main className="pb-24 px-4 sm:px-6 lg:px-8 pt-2.5 relative z-10">
                <div className="max-w-4xl mx-auto">

                    {/* Componente para la navegacin de regreso. Muestra un enlace para "Volver al Inicio". */}
                    <ContextualBackNavigation
                        customBackPath="/home-dashboard"
                        customLabel="Volver al Inicio"
                        showBreadcrumb={true}
                        className="mb-8" />

                    {/* --- Encabezado Visual de la Pgina --- */}
                    <div className="text-center mb-12">
                        {/* Crculo con el cono de Informacin, con un degradado y una animacin de pulso. */}
                        <div className="w-20 h-20 bg-gradient-to-br from-primary via-accent to-secondary rounded-full flex items-center justify-center mx-auto mb-6 shadow-graffiti-lg animate-pulse-glow">
                            <Icon name="Info" size={36} className="text-white" />
                        </div>
                        {/* Ttulo principal de la pgina con un efecto de texto degradado. */}
                        <h1 className="font-heading text-4xl md:text-5xl text-transparent bg-gradient-to-r from-primary via-accent to-secondary bg-clip-text mb-4">
                            Acerca de Kamikaze
                        </h1>
                        {/* Subttulo o eslogan de la pgina. */}
                        <p className="font-body text-lg text-text-secondary max-w-2xl mx-auto">
                            El juego de beber definitivo que transformar tus reuniones en experiencias picas e inolvidables
                        </p>
                    </div>

                    {/* --- Secciones de Contenido Informativo --- */}
                    {/* Este contenedor agrupa todas las secciones principales de la pgina. */}
                    <div className="space-y-8">
                        <GameDescriptionSection />      {/* Seccin que explica qu es el juego. */}
                        <GameFeaturesGrid />            {/* Seccin que muestra las caractersticas principales. */}
                        <ChallengeProposalSection />    {/* Seccin que invita a los usuarios a proponer retos. */}
                        <ResponsibleDrinkingReminder /> {/* Recordatorio sobre el consumo responsable de alcohol. */}
                        <InstallationGuideSection />    {/* Gua para instalar la app (PWA). */}
                        <CommunityGuidelinesSection />  {/* Reglas de la comunidad. */}
                    </div>

                    {/* --- Informacin de Pie de Pgina y Crditos --- */}
                    <div className="mt-12 text-center">
                        <div className="bg-surface/30 p-6 rounded-lg border border-border">
                            <div className="flex items-center justify-center space-x-2 mb-3">
                                <Icon name="Calendar" size={18} className="text-text-secondary" />
                                <span className="font-body text-text-secondary">
                                    {/* Obtiene el ao actual dinmicamente para mantener la fecha actualizada sin editar el cdigo. */}
                                    ltima actualizacin: Octubre {new Date()?.getFullYear()}
                                </span>
                            </div>
                            {/* Prrafo de crditos y contexto del proyecto. */}
                            <p className="font-body text-sm text-text-secondary">
                                Kamikaze es un proyecto de escrito con el corazn y cdigo, inspirado en noches con caguamas y jueguitos para empedarnos.
                                <br />
                                Dev. Sebastian Tapia
                            </p>
                            {/* Pequeos conos con puntos clave sobre el proyecto. */}
                            <div className="flex items-center justify-center space-x-6 mt-4 text-sm text-text-secondary">
                                <div className="flex items-center space-x-1">
                                    <Icon name="Users" size={14} />
                                    <span>Comunidad Global</span>
                                </div>
                                <div className="flex items-center space-x-1">
                                    <Icon name="Zap" size={14} />
                                    <span>PWA Nativo</span>
                                </div>
                                <div className="flex items-center space-x-1">
                                    <Icon name="Shield" size={14} />
                                    <span>Juego Responsable</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    );
};

export default AboutInformation;